.title{    
    margin:0.2em;
    padding: 0 .5em;
    text-align: center;
}
.subTitle{
    margin:0;
}
.telephone{
    margin:0 15px;
}
.table{
    border-collapse: collapse;
          margin: 5px auto;
          width: 95%;

}
.table td{
    border: 1px solid #000;
    padding: 4px 8px;
}

.table td:first-child,
.table td:nth-child(3) {
    font-weight: bold;
}
.logo{
    width: 6em;
    padding: .5em 0 0 0;
}